import React, {useEffect, useState} from 'react'
import axios, {cacheAdapterEnhancer} from 'axios'
import Speech from 'react-speech';
// import styled from 'styled-components'

// const Text = styled.div`
//     font-size: 40px;
//     color: hotpink;
//     font-weight: 900;
// `

function CoinPrice() {
    const [data, setData] = useState()

    useEffect(() => {    
        search()     
        setInterval(() => {
            search()     
        }, 10000)
    }, [])
  
    const search = async () => {

      const res = await axios.get('https://api.bithumb.com/public/ticker/BLY_KRW' + '_' + new Date().getTime());
      

      console.log(res.data.data.closing_price)
      setData(res.data.data)

      document.getElementsByTagName('button')[0].click()
      
    }
  
  
    if(!data) return null
  
    return (
        <div style={{
            flexDirection: 'column',            
            height: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>

            <div style={{
                fontSize: 30,
                color: 'darkgray',
                marginRight: 10
            }}>
                <div>블로서리 시세</div>
                <div>{data.closing_price}</div>
            </div>
                <Speech                
                    text={data.closing_price}
                    resume={true} 
                />
            
        </div>
    );
}
  
export default CoinPrice